@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: none;
  border: none;
  background: none;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
}

footer {
  margin-top: auto;
}

.container {
  width: 90%;
  max-width: 1440px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.mt-5, .my-5 {
  margin-top: 3rem;
}

.mt-4, .my-4 {
  margin-top: 1.5rem;
}

.mt-3, .my-3 {
  margin-top: 1rem;
}

.mt-2, .my-2 {
  margin-top: .5rem;
}

.mt-1, .my-1 {
  margin-top: .25rem;
}

.mt-auto, .my-auto {
  margin-top: auto;
}

@media (max-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media (max-width: 680px) {
  .container {
    max-width: 680px;
  }
}


.header-covid {
  background-color: #02c26a;
  width: 100%;
  color: #FFFFFF;
  align-items: center;
  margin: 0 auto;
  justify-content: space-between;
}

.header-covid .logo-covid {
  font-size: 1.2em;
  font-weight: 700;
  text-transform: uppercase;
}

.header-covid .info span {
  padding: 12px;
}

.header-covid .info a:hover {
  color: #c0c0;
}
.section-covid {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  max-width: 1140px;
  margin: 60px auto;
}

.section-covid .search {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.section-covid .search form {
  width: 50%;
}

.section-covid .search form select {
  border: 1px solid #000000;
  width: 100%;
  padding: 15px;
  border-radius: 8px;
}

option {
  border-radius: 8px !important;
  font-weight: normal;
  display: block;
  white-space: pre;
  min-height: 1.2em;
  padding: 0px 2px 1px;
}

.section-covid .cards {
  display: flex;
  flex-wrap: wrap;
  margin: 40px auto;
}

.section-covid .card {
  flex: 1 1 260px;
  width: 260px;
  height: 180px;
  margin: 10px;
  border-radius: 10px;
  box-shadow: 6px 6px 6px -8px rgba(0,0,0,.3);
  color: #FFFFFF;
  transition: 400ms;
}

.section-covid .card:hover {
  box-shadow: 4px 4px 4px -2px rgba(0,0,0,.3);
}

.section-covid .card:nth-child(1) {
  background: #09D914;
}

.section-covid .card:nth-child(2) {
  background: #e4c800;
}

.section-covid .card:nth-child(3) {
  background: #808080;
}

.section-covid .card .number-cases {
  width: 100%;
  padding: 24px;
  margin: 24px auto;
  text-align: center;
}

.section-covid .card .cases-content {
  width: 100%;
  text-align: center;
  border-top: 1px solid #FFFFFF;
  padding: 18px;
}

.section-covid .card .cases-content .card-title {
  font-size: 1rem;
  text-transform: uppercase;
}

.section-covid .card .number-cases .card-text {
  font-size: 1.6em;
  text-transform: uppercase;
  font-weight: 700;
}

.section-covid .info-locale-date {
  display: flex;
  width: 100%;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.section-covid .info-locale {
  margin-bottom: .5rem;
  font-size: 1rem;
  font-weight: 500;
}

.section-covid .info-date {
  font-weight: 500;
  font-size: 1rem;
}

@media (max-width: 680px) {
  .section-covid .search form {
    width: 90%;
  }
}
.footer-covid {
  background-color: #02C26A;
  color: #FFFFFF;
  width: 100%;
  justify-content: space-between;
}

.footer-covid .redes span {
  padding: 20px;
}

.footer-covid p {
  font-size: 1em;
  font-weight: 500;
}
